import React, { Component } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import "../../style.css"
import NumbersAPI from "../../Component/NumbersAPI/NApi"
import CompoundInterest from '../../Component/calculatorSecond/calculatorsecond'
import SecondAPI from '../../Component/SecondAPI/SecondAPI'
import PMS from '../Home/images/Portfolio Management Service/1.png'
import PMSF1 from '../Home/images/Portfolio Management Service/Asset Allocation.svg'
import PMSF2 from '../Home/images/Portfolio Management Service/Customization.svg'
import PMSF3 from '../Home/images/Portfolio Management Service/Portfolio Performance Tracking.svg'
import PMSF4 from '../Home/images/Portfolio Management Service/Risk Management.svg'
import PMSF5 from '../Home/images/Portfolio Management Service/Maintaining Liquidity.svg'
import PMSF6 from '../Home/images/Portfolio Management Service/Knowledge.svg'
import PartnerPMS from '../../Component/PartnerPMS/PartnerPMS'
import reactHtmlParser from 'react-html-parser';
import { withTranslation } from 'react-i18next'
import {Helmet} from 'react-helmet';

export class pms extends Component {

   constructor() {
      super()
   }

   data = {
      "PMS": {
         "Title": "",
         "ProductCategoryId": "3",
         "SebiCategory": "",
         "SebiSubCategory": "",
         "DefaultProductId": "1",
      }
   }

   render() {
      const productinfo = this.data.PMS;
      const{t} = this.props;

      return (
         <section>

         <Helmet>
                <meta charSet="utf-8" />
                <title>{t('MetaTitle:mT63')}</title>
                <meta name="description" content={t('MetaDescription:mD63')} />
            </Helmet>

            <div className="container container-wrapper">
               <div className="row">
                  <div className="col-lg-7 col-md-6 mb-4">
                     <h2>{t('PMS:pmsP1')}</h2>
                     <p>{t('PMS:pmsP2')}</p>

                  </div>
                  <div className="col-lg-5 col-md-6"><img src={PMS} className='img-fluid' /></div>
               </div>
            </div>

            <div className="API-Section">
               <div className="container container-wrapper">
                  <h2 className='text-center'>{t('PMS:pmsP3')}</h2>
                  <div className='row row justify-content-lg-center'>
                     <div className='col col-lg-10'>
                        <SecondAPI productCategoryId={productinfo.ProductCategoryId} sebiCategory={productinfo.SebiCategory} sebiSubCategory={productinfo.SebiSubCategory} defaultProductId={productinfo.DefaultProductId} />
                     </div>
                  </div>
               </div>
            </div>

            <div className="container features ourPartCaptital">
               <div className="row">
                  <div className="col-lg-12">
                     <h2> {t('PMS:pmsP4')}</h2>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={PMSF1} />
                        <h4>{t('PMS:pmsP5')}</h4>
                        <p className='small-text'>{t('PMS:pmsP6')}</p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={PMSF2} />
                        <h4>{t('PMS:pmsP7')}</h4>
                        <p className='small-text'>{t('PMS:pmsP8')}</p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={PMSF3} />
                        <h4>{t('PMS:pmsP9')}</h4>
                        <p className='small-text'>{t('PMS:pmsP10')}</p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={PMSF4} />
                        <h4>{t('PMS:pmsP11')}</h4>
                        <p className='small-text'>{t('PMS:pmsP12')}</p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={PMSF5} />
                        <h4>{t('PMS:pmsP13')}</h4>
                        <p className='small-text'>{t('PMS:pmsP14')}</p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={PMSF6} />
                        <h4>{t('PMS:pmsP15')}</h4>
                        <p className='small-text'>{t('PMS:pmsP16')}</p>
                     </div>
                  </div>
               </div>
            </div>
            <div className="getCustomer">
               <div className="container container-wrapper pms-card">
                  <h2>{t('PMS:pmsP17')}</h2>
                  <p>{t('PMS:pmsP18')}</p>
                  <div className="row row justify-content-md-center ">
                     <div className=" col col-lg-4 col-md-6">

                        <div className="card mb-4 h-100">
                           <div className="card-body">
                              <h3>{t('PMS:pmsP19')}</h3>
                              <p className='small-text'>{t('PMS:pmsP20')}</p>
                           </div>
                        </div>

                     </div>
                     <div className="col col-lg-4 col-md-6">

                        <div className="card mb-4 h-100">
                           <div className="card-body">
                              <h3>{t('PMS:pmsP21')}</h3>
                              <p className='small-text'>{t('PMS:pmsP22')}</p>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>

            <PartnerPMS />

            <div className="landingPlatforms">
               <div className="container">
                  <div className="row">
                     <h2>{t('PMS:pmsP23')}</h2>
                     <p>{t('PMS:pmsP24')}</p>
                     <p>{t('PMS:pmsP25')}</p>
                     <p>{t('PMS:pmsP26')}</p>
                     <p>{t('PMS:pmsP27')}</p>
                  </div>
               </div>
            </div>

            <div className="container container-wrapper">
               <h2>{t('PMS:pmsP28')}
               </h2>
               <Tabs>
                  <TabList>
                     <Tab>{t('PMS:pmsP29')}</Tab>
                     <Tab>{t('PMS:pmsP34')}</Tab>
                     <Tab>{t('PMS:pmsP38')}</Tab>
                  </TabList>
                  <TabPanel>
                     <ul>
                        <li>{t('PMS:pmsP30')}</li>
                        <li>{t('PMS:pmsP31')}</li>
                        <li>{t('PMS:pmsP32')}</li>
                        <li>{t('PMS:pmsP33')}</li>
                     </ul>
                  </TabPanel>
                  <TabPanel>
                     <ul>
                        <li>{t('PMS:pmsP35')}</li>
                        <li>{t('PMS:pmsP36')}</li>
                        <li>{t('PMS:pmsP37')}</li>
                     </ul>
                  </TabPanel>
                  <TabPanel>
                     <ul>
                        <li>{t('PMS:pmsP39')}</li>
                        <li>{t('PMS:pmsP40')}</li>
                        <li>{t('PMS:pmsP41')}</li>
                     </ul>
                  </TabPanel>
               </Tabs>
            </div>

            <NumbersAPI />

            <div className="calCulator">
               <div className="container container-wrapper">
                  <h2 className='mb-4'>PMS Calculator</h2>
                  <CompoundInterest />
               </div>
            </div>

            <div className="container testimonials">
               <div className="row">
                  <div className="col-lg-12">
                     <h2>{t('PMS:pmsP42')}</h2>
                     <div id="accordion">
                        <h3>{t('PMS:pmsP43')}</h3>
                        <p>{t('PMS:pmsP44')}</p>

                        <h3>{t('PMS:pmsP45')}</h3>
                        <p>{t('PMS:pmsP46')}</p>

                        <h3>{t('PMS:pmsP47')}</h3>
                        <p>{t('PMS:pmsP48')}</p>

                        <h3>{t('PMS:pmsP49')}</h3>
                        <p>{t('PMS:pmsP50')}</p>

                        <h3>{t('PMS:pmsP52')}</h3>
                        <p>{t('PMS:pmsP53')}</p>

                        <h3>{t('PMS:pmsP54')}</h3>
                        <p>{t('PMS:pmsP55')}<br/>{reactHtmlParser(t('PMS:pmsP56'))}</p>

                        <h3>{t('PMS:pmsP57')}</h3>
                        <p>{t('PMS:pmsP58')}<br/>{t('PMS:pmsP59')}<br/>{t('PMS:pmsP60')}</p>

                        <h3>{t('PMS:pmsP61')}</h3>
                        <p>{reactHtmlParser(t('PMS:pmsP62'))}</p>

                     </div>
                  </div>
               </div>
            </div>

         </section>
      )
   }
}

export default withTranslation() (pms)
