import React, { Component } from 'react'
import Tata from '../../Layouts/Home/images/Portfolio Management Service/Tata Asset Management Company.svg'
import Motilal from '../../Layouts/Home/images/Portfolio Management Service/Motilal Oswal Group.svg'
import ICICI from '../../Layouts/Home/images/Portfolio Management Service/ICICI Prudential Portfolio Management.svg'
import ASK from '../../Layouts/Home/images/Portfolio Management Service/ASK Group.svg'
import Invesco from '../../Layouts/Home/images/Portfolio Management Service/Invesco Asset Management India Pvt. Ltd..svg'
import { withTranslation } from 'react-i18next'

export class PartnerPMS extends Component {
    render() {
      const{t}=this.props;
        return (
            <div className='ourPartCaptital'>
            <div className='container container-wrapper'>
               <h2 className='text-center'>Our Partners</h2>
               <div className='row row justify-content-center'>
                  <div className='col-lg-4 col-md-6 mt-4'>
                     <div className='card h-100'>
                        <div className='card-body'>
                           <img src={Tata} />
                           <h4>Tata Asset Management Company</h4>
                           <p>Know More About PNB Housing Finance Ltd. </p>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/pms/tata-asset-management" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-4 col-md-6 mt-4'>
                     <div className='card h-100'>
                        <div className='card-body'>
                           <img src={Motilal} />
                           <h4>Motilal Oswal Group</h4>
                           <p>Know More About HDFC Limited  </p>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/pms/motilal-oswal-group" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-4 col-md-6 mt-4'>
                     <div className='card h-100'>
                        <div className='card-body'>
                           <img src={ICICI} />
                           <h4>ICICI Prudential Portfolio Management</h4>
                           <p>Know More About ICICI Home Finance Company Ltd </p>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/pms/icici-prudential" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-4 col-md-6 mt-4'>
                     <div className='card h-100'>
                        <div className='card-body'>
                           <img src={ASK} />
                           <h4>ASK Group</h4>
                           <p>Know More About Shriram Transport Finance Limited</p>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/pms/ask-group" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-4 col-md-6 mt-4'>
                     <div className='card h-100'>
                        <div className='card-body'>
                           <img src={Invesco} />
                           <h4>Invesco Asset Management India Pvt. Ltd.</h4>
                           <p>Know More About Bajaj Finance Limited </p>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/pms/invesco-asset-management" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
               </div>
            </div>
            </div>
        )
    }
}

export default withTranslation() (PartnerPMS)
